.Users {
    margin: 50px;
}

.imageContainer {
    height: 140px;
    overflow: hidden;

}

.userPhoto {
    width: 140px;


}

.unfollowBtn, .followBtn, .getUsersBtn {
    display: block;
    margin-top: 5px;
    width: 140px;
    height: 45px;
    border: none;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: bold;
    outline: none;
    cursor: pointer;

}

.unfollowBtn {
    background: #F2856D;
}

.followBtn {
    background: #A0D9AE;
}
