.Login {
    width: 50%;
    margin: 0 auto;
    margin-top: 80px;

}

.Login h3 {
    margin-bottom: 20px;
    font-size: 25px;
}


.Login input {
    display: block;
    width: 320px;
    height: 30px;
    padding: 10px;
    margin-bottom: 10px;
    border: none;

}

.Login input:focus {
    outline-color: #F2856D;


}



.Login .checkbox {
    display: inline-block;
    width: 15px;
    margin-left: 5px;
    margin-bottom: 0;
}

.Login span {
    margin-left: 12px;
    position: absolute;
    transform: translateY(3px);

}

.Login button {
    display: block;
    margin-top: 5px;
    width: 140px;
    height: 45px;
    border: none;
    text-transform: uppercase;
    font-size: 10px;
    font-weight: bold;
    outline: none;
    cursor: pointer;
    transition: all 0.2s;


}

.Login button:hover {

    background: #F2856D;
    
}

.formSummaryError {
    width: 70%;
    border: 1px solid red;
    padding: 5px;
    color: orangered;
}

.captchaInput {
    margin-top: 10px;
}