.paginationWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin-top: 30px;



}

.paginationWrapper span {
    justify-content: center;
    box-sizing: border-box;
    padding: 2px 10px;
    margin-left: 1px;
    text-align: center;
    text-transform: uppercase;
    position: relative;
    overflow:hidden;
    transition: all .2s;
    cursor: pointer;


}





.paginationWrapper span:hover {
    color: #fafafa;
    background: #F2856D;
    border-radius: 2px;
}


.paginationWrapper .activePage {
    color: #fafafa;
    background: #F2856D;
    border-radius: 2px;
}

.paginationWrapper .paginationPrev {

    font-size: 12px;
    margin-right: 10px;
    color: #fafafa;
    background: #F2856D;
    border-radius: 2px;

}

.paginationWrapper .paginationNext {
    box-sizing: border-box;
    font-size: 12px;
    margin-left: 10px;
    color: #fafafa;
    background: #F2856D;
    border-radius: 2px;


}


.paginationWrapper .paginationPrev:hover, .paginationWrapper .paginationNext:hover {
    background: none;
    border-radius: 2px;
    box-shadow: 0 0 0 2px #F2856D;
    color: #262626;


}
