.AvatarBtn {
    margin: 15px;
}

.AvatarBtn button {
    display: block;
    width: 220px;
    height: 40px;
    background: #A0D9AE;

    border: 0px solid lightgray;
    margin-top: 8px;
    color: #000;
    transition: all 0.4s;

}


.AvatarBtn button:hover {
    background: none;
    border: 1px solid lightgray;


}