.formControl {
    width: 50%;
    min-width: 50%;
}

.textArea {

    resize: none;
    min-width: 100%;
    min-height: 100px;
    border: none;
    padding: 15px 15px;
    background-color: #fff;
    font-size: 18px;
    transition: all 0.2s;
}


.textArea:focus {
    outline: none;
    background: #fcfcfc;
}


.formControl.error textarea,
.formControl.error input {

    display: inline-block;
    border: 1px solid indianred;
}
.formControl.error {
    position: relative;
}

.formControl.error span {
    display: block;
    position: absolute;
    text-align: center;
    bottom: -25px;
    right: 0;
    margin-left: 10px;
    color: indianred;
}

