.header {

    grid-area: h;
    background: whitesmoke;



}

.header img {

    padding-top: 5px;
    padding-left: 5px;
    width: 70px;

}

.headerRight {
    float: right;
}

.header p {
    display: inline-block;
    margin-right: 20px;
    margin-top: 10px;
    font-size: 30px;
    text-transform: uppercase;
    font-weight: bold;
    color: #F2856D;


}

.header p span {
    font-size: 10px;
    vertical-align: middle;
    color: #626262;


}

.loginBlock {
    display: inline-block;
    transform: translateY(-3px);
    margin-right: 30px;


}

.loginBtn {

    text-decoration: none;
    font-size: 18px;
    cursor: pointer;
    transition: all .2s;

}

.loginBtn:hover {
    color: #F2856D;
}

.loginBtn:first-child {
    margin-right: 15px;

}

.logoutBtn {
    background: none;
    border: none;
    font-size: 12px;
    color: lightskyblue;
    margin-left: 10px;
    cursor: pointer;
}
