.Navbar {
    grid-area: n;
    position: relative;
    background: #F2856D;
    min-height: 100vh;
}


.nav {
    padding: 20px;
}

.item {
    margin-top: 10px;
    position: relative;

}


.item:first-child {
    margin-top: 30px;
}

.item a {
    color: #ffffff;
    text-decoration: none;
    font-size: 20px;
    font-weight: normal;
    transition: all 0.1s;
}

.item a:hover {
    color:  #495073;
    text-decoration: none;
    font-size: 20px;
}


.item a.active {
    color: #495073;
    position: relative;
    left: 3px;
    font-weight: 500;




}
